"use strict";
var Product = require('./product');
var ProductView = require('./productView');
var ProductManager = (function () {
    function ProductManager() {
        this.products = {};
        this.productView = new ProductView('');
        this.fireBase = new Firebase('https://sizzling-torch-925.firebaseio.com/shopping/products');
    }
    ProductManager.prototype.getProducts = function () {
        var _this = this;
        this.fireBase.on("value", function (snapshot) {
            _this.updateProducts(snapshot.val());
            _this.updateView();
        }, function (errorObject) {
            console.log("The read failed: " + errorObject.code);
        });
    };
    ProductManager.prototype.addProduct = function (product) {
        this.fireBase.push(product);
    };
    ProductManager.prototype.removeProduct = function (id) {
        if (this.products[id]) {
            this.products[id].remove();
        }
    };
    ProductManager.prototype.updateProducts = function (res) {
        var _this = this;
        var config;
        Object.keys(res).map(function (id) {
            config = res[id];
            _this.products[id] = new Product(id, config);
        });
    };
    ProductManager.prototype.updateView = function () {
        this.productView.render(this.products);
    };
    return ProductManager;
}());
module.exports = ProductManager;



