"use strict";
var ProductView = (function () {
    function ProductView(template) {
        this.template = template;
        this.view = document.getElementById('view');
    }
    ProductView.prototype.render = function (products) {
        var product;
        this.template = Object.keys(products).map(function (id) {
            product = products[id];
            return "<li>\n\t\t\t\t" + product.name + "\n\t\t\t</li>";
        }).join('');
        this.view.innerHTML = this.template;
    };
    return ProductView;
}());
module.exports = ProductView;



